import React from "react";
import "../../css/HomePage.css";
import { Swiper, SwiperSlide } from "swiper/react";
import { Navigation, Pagination } from "swiper/modules";
import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/pagination";
import { Link } from "react-router-dom";

interface Project {
  title: string;
  description: string;
  technologies: string[];
  image: string;
  link?: string;
}

const projects: Project[] = [
  {
    title: "Driver Monitoring Application",
    description: "   ,    ,    .",
    technologies: ["React", "JavaScript", "Django",""],
    image: require("../../images/project/dma.png"),
    link: "/dmapage"
  },
  {
    title: "FestaNow",
    description: "            . ( )",
    technologies: ["React Native", "TypeScript", "Spring Boot", "1 "],
    image: require("../../images/project/festanow.png"),
    link: "/festanowpage"
  },
  {
    title: "Cullinan",
    description: "        ,  ",
    technologies: ["Unity", "C#", "Excel", "Jira"],
    image: require("../../images/project/cullinan.png"),
    link: "/cullinanpage"
  }
];

const ProjectsContent: React.FC = () => {
  return (
    <section id="projects" className="projects">
      <h2>PROJECT</h2>
      {/*   */}
      <div className="projects-slider-container">
        <Swiper
          modules={[Navigation, Pagination]}
          navigation
          pagination={{ clickable: true }}
          slidesPerView={1} //     
          className="projects-slider"
        >
          {projects.map((project, index) => (
            <SwiperSlide key={index}>
              <div className="project-card">
                <div className="project-card-left">
                <img src={project.image} alt={project.title} className="project-image" />
                <div className="tech-stack">
                    {project.technologies.map((tech, idx) => (
                      <span key={idx} className="tech-tag">{tech}</span>
                    ))}
                  </div>
                </div>

                <div className="project-card-right">
                <div className="project-info">
                  
                  <h3>{project.title}</h3>
                  <p>{project.description}</p>
                  
                  {project.link && (
  <Link to={project.link} className="project-link">
      
  </Link>
)}
                </div>
              </div>
              </div>
            </SwiperSlide>
          ))}
        </Swiper>
      </div>
    </section>
  );
};

export default ProjectsContent;
